#app {
  max-width: 900px;
  margin: 0 auto;
}

summary {
  text-align: center;
  margin-bottom: 1rem;
  color: var(--yellow);
  cursor: pointer;
}

.mails-container {
  margin-bottom: 2rem;
}

.hide-res {
  display: none;
}

section {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}
section label {
  width: 600px;
}
section #search {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: var(--background);
  color: var(--text-color);
}

aside {
  position: absolute;
  top: 70px;
  right: 10;
  padding: 5px;
  border-radius: 5px;
  background-color: #222;
  max-width: 300px;
}
aside ul {
  list-style: none;
}
aside li {
  cursor: pointer;
}
aside li:hover {
  text-decoration: underline;
}